<template>
    <div>
        <mt-header fixed title="批转"/>
        <div class="mint-content soft-scrollable">

            <div class="smallFont textGray media-tip gap bdr-t">长按可删除图片</div>
            <add-media
                    class="bdr-b"
                    :originImgList="originImgList"
                    ref="addMedia"
                    @onImgListChange="onImgListChange($event)"></add-media>

            <div v-show="isNormalUser" class="transInfo gap bdr-t bdr-b white-btn" @click="selectTransInfo">
                <span>选择流向：</span><span class="textBlue">{{transInfoText}}</span>
            </div>

            <div class="gap bdr-t bdr-b">
                <textarea class="default report-field"
                          v-model="opinion"
                          rows="6"
                          placeholder="请输入意见">
                </textarea>
            </div>

            <mt-button class="report-btn" type="default" @click.native="confirm">{{isEdit ? '保存编辑' : '提交'}}</mt-button>
        </div>
        <info-picker
                ref="picker"
                @confirm="onTransInfoSelect"
                v-model="transInfo"
                :groups="transInfoGroups"></info-picker>
    </div>
</template>
<style scoped>
    .report-btn {
        width: 90%;
        margin-top: 25px;
        margin-left: 5%;
        margin-bottom: 20px;
    }

    .media-tip {
        padding: 10px 10px 0 10px;
        background-color: white;
    }

    .transInfo {
        padding: 10px;
        background-color: white;
    }
</style>
<script>
    import {Indicator, MessageBox, Toast} from 'mint-ui';
    import Util from 'Util'
    import http from 'Http'
    import {verifyState} from '../../verify'
    import {data as gData} from 'zhixin'
    import TransInfoPicker from '../widget/TransInfoPicker.vue'
    import AddMedia from '../widget/AddMedia.vue'
    import {formatTaskBo, uploadWxMedia} from '../../helper'

    /**
     * 包括内部人员批转案件、外部人员填写和编辑信息功能
     */
    function defaultData() {
        return {
            taskBo: {},
            opinion: '',
            srcList: [],
            transInfoGroups: [],
            transInfo: null,
            shareMode: false,
            isNormalUser: false,
            originImgList: [],
            originImgBackupList: [],
            isEdit: false,
            editInst: {}
        }
    }
    export default{
        data(){
            return defaultData()
        },
        components: {
            'add-media': AddMedia,
            'info-picker': TransInfoPicker
        },
        computed: {
            transInfoText() {
                return this.transInfo
                        ? this.transInfo.name
                        : '请选择'
            }
        },
        methods: {
            init() {
                this.initPageInst(defaultData())
                this.$refs.addMedia.reset()
                this.$refs.picker.reset()

                this.shareMode = gData.shareMode
                this.isNormalUser = gData.selfFlag != 1

                if (this.shareMode) {
                    this.taskBo.recID = Util.getRequestParam('recID')
                    this.taskBo.actDefID = Util.getRequestParam('actDefID')
                    this.taskBo.actID = Util.getRequestParam('actID')
                } else {
                    let params = this.$route.params
                    this.taskBo = params && params.taskBo || {}
                }
                formatTaskBo(this.taskBo)

                if (this.isNormalUser) {
                    this.loadTransInfo()
                } else {
                    this.loadTransLog()
                }
            },
            selectTransInfo() {
                this.$refs.picker.open()
            },
            onTransInfoSelect(val) {
                this.transInfo = val
            },
            onImgListChange(list) {
                this.srcList = list || []
            },
            diffImgIdStr() {
                let map = {}
                this.originImgList.forEach(item=> {
                    map[item.mediaID] = true
                })
                return this.originImgBackupList
                        .filter(item=> !map[item.mediaID])
                        .map(item=> item.mediaID)
                        .join(',')
            },
            checkInput() {
                if (!this.opinion) {
                    Toast("请填写意见")
                    return false
                }

                if (this.isNormalUser && !this.transInfo) {
                    Toast("请选择流向")
                    return false
                }

                if (this.srcList.length + this.originImgList.length > 9) {
                    Toast("图片不能多于9张")
                    return false
                }
                return true
            },
            confirm() {
                if (!this.checkInput()) {
                    return
                }
                let doneAction = imageIds=> {

                    let methodName = this.isNormalUser
                            ? "api/v2/wxPublic/dealHelperTransit"
                            : "api/v2/wxPublic/agencyassign"
                    let params = {
                        humanID: gData.humanID,
                        recID: this.taskBo.recID,
                        actID: this.taskBo.actID,
                        actDefID: this.taskBo.actDefID,
                        opinion: this.opinion,
                        wavNum: 0,
                        picNum: imageIds.length,
                        uniqueID: new Date().getTime(),
                        mediaStr: JSON.stringify(imageIds.map(id=> {
                            return {
                                wxMediaID: id,
                                wxMediaType: 0,
                                wxMediaDisplay: ""
                            }
                        })),
                    }
                    if (this.isNormalUser) {
                        params.transDirection = 1
                        params.transInfo = `${this.transInfo.NextActDefID},${this.transInfo.NextPartID},`
                    } else {
                        params.itemID = this.isEdit ? this.editInst.itemID : '-1'
                        params.deleteMediaIDs = this.diffImgIdStr()
                        params.editTypeID = this.isEdit ? 1: 0
                    }

                    http.ajax({
                        methodName: methodName,
                        params: params,
                        start: ()=> {
                            Indicator.open("正在处理")
                        },
                        success: (data) => {
                            if (data) {
                                if (data.success) {
                                    Toast("提交成功")
                                    if (!gData.shareMode) {
                                        Util.doCallback(this)
                                        Util.goBack()
                                    } else {
                                        Util.leavePlugin()
                                    }
                                } else if (data.message) {
                                    Toast(data.message)
                                }
                            }
                        },
                        error(r1, r2) {
                            alert(JSON.stringify(r2))
                        },
                        final: () => {
                            Indicator.close()
                        }
                    })
                }
                uploadWxMedia(this.srcList, doneAction)
            },
            loadTransLog() {
                http.ajax({
                    methodName: "/api/v2/wxPublic/agencyassignlog",
                    params: {
                        humanID: gData.humanID,
                        actID: this.taskBo.actID,
                        recID: this.taskBo.recID,
                    },
                    start: ()=> {
                        Indicator.open("获取信息")
                    },
                    success: (data) => {
                        if (data && data.success) {
                            let inst = data.data && data.data.wfItemInst
                            if (inst) {
                                this.isEdit = true
                                this.editInst = inst
                                this.opinion = inst.itemContent
                            }
                            let mediaInfo = data.data && data.data.mediaInfo || []
                            this.originImgBackupList = mediaInfo
                            this.originImgList = this.originImgBackupList.slice(0)
                            this.originImgList.forEach(item=> {
                                item.src = item.mediaUrl
                            })
                        } else if (data) {
                            Toast(data.message)
                        }
                    },
                    final: () => {
                        Indicator.close()
                    }
                })
            },
            loadTransInfo() {
                http.ajax({
                    functionName: "getTransInfo_ZX",
                    params: {
                        cardID: gData.cardID,
                        humanID: gData.humanID,
                        actID: this.taskBo.actID,
                        recID: this.taskBo.recID,
                        transDirection: 1
                    },
                    start: ()=> {
                        Indicator.open("获取案件流向")
                    },
                    success: (data) => {
                        if (data) {
                            if (data.success) {
                                let list = data.data && data.data.list || []
                                let groupValues = {}
                                let groups = []
                                list.forEach(item=> {
                                    let gId = item.GroupID
                                    if (!groupValues[gId]) {
                                        groupValues[gId] = []
                                        groups.push({
                                            groupID: item.GroupID,
                                            name: item.NextActDefName,
                                            values: groupValues[gId],
                                            isGroup: true
                                        })
                                    }
                                    item.groupID = item.GroupID
                                    item.name = item.NEXTPARTNAME
                                    groupValues[gId].push(item)
                                })
                                this.transInfoGroups = groups
                            } else {
                                alert(data.message)
                            }
                        }
                    },
                    final: () => {
                        Indicator.close()
                    }
                })
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm=> {
                Util.setTitle('案件处置')
                if (vm.isNewPage(to)) {
                    verifyState({
                        from: 'transist',
                        inst: vm
                    }).then(()=> {
                        vm.init()
                    })
                }
            })
        },
    }
</script>



