<template>
    <mt-popup v-model="visible" position="bottom" class="mint-datetime">
        <mt-picker
                :slots="groupSlots"
                @change="onChange"
                valueKey="name"
                :visible-item-count="7"
                class="mint-datetime-picker"
                ref="picker"
                show-toolbar>
            <span class="mint-datetime-action mint-datetime-cancel" @click="visible = false">取消</span>
            <span class="mint-datetime-action mint-datetime-confirm" @click="confirm">确定</span>
        </mt-picker>
    </mt-popup>
</template>
<style>
</style>
<script>
    import Util from 'Util';

    function defaultData() {
        return {
            visible: false,
            currentValue: null,
            groupSlots: []
        }
    }
    export default{
        props: {
            groups: {
                type: Array,
                default: []
            },
            value: null
        },
        data(){
            return defaultData()
        },
        computed: {},
        methods: {
            reset() {
                Util.resetObject(this, defaultData())
            },
            onChange(picker, values){
                console.log(values)
                if (values.length > 0) {
                    if (values[0] && values[0].isGroup) {
                        this.currentValue = values[0].values[0]
                    } else {
                        this.currentValue = values[0]
                    }
                }
                this.generateSlots()
            },
            confirm() {
                this.visible = false;
                this.$emit('confirm', this.currentValue);
            },
            open() {
                this.visible = true
            },
            close() {
                this.visible = false
            },
            generateSlots() {
                this.groupSlots = []

                if (this.groups.length == 0) {
                    return
                }

                if (!this.currentValue) {
                    this.currentValue = this.groups[0].values[0]
                }

                let selectedGroup = this.groups[0]
                this.groups.forEach(item=> {
                    if (item.groupID == this.currentValue.groupID) {
                        selectedGroup = item
                    }
                })

                this.groupSlots = [
                    {
                        flex: 1,
                        values: this.groups
                    }, {
                        divider: true,
                        content: '-',
                    }, {
                        flex: 1,
                        values: selectedGroup.values
                    }
                ]

                const setSlotValue = this.$refs.picker.setSlotValue
                this.$nextTick(() => {
                    setSlotValue(0, selectedGroup)
                    setSlotValue(1, this.currentValue)
                })
            }
        },
        watch: {
            value(val) {
                this.currentValue = val
            },
            groups(val) {
                this.generateSlots()
            },
        },
        mounted() {
            this.currentValue = this.value
            this.generateSlots()
        }
    }
</script>



